.main-nav {
  position: relative;
  width: 100%;
  min-height: 56px;
  z-index: 3;
}
@media (min-width: 768px) {
  .main-nav {
    z-index: 2;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  width: 63px;
  height: 56px;
  border: none;
  background-color: inherit;
  background-repeat: no-repeat;
  background-position: center center;
  z-index: 4;
  cursor: pointer;
}
.main-nav__toggle:hover,
.main-nav__toggle:focus {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--nojs {
    margin-bottom: 56px;
  }
  .main-nav--nojs::before {
    position: absolute;
    content: "";
    left: 111px;
    bottom: -82px;
    width: 102px;
    height: 82px;
    background-image: url("../img/logotype-mobile.svg");
    background-repeat: no-repeat;
    z-index: 2;
  }
  .main-nav--opened .main-nav__list {
    position: absolute;
    left: 0;
    right: 0;
  }
  .main-nav--opened .main-nav__toggle::before {
    position: absolute;
    content: "";
    top: 26px;
    right: 18px;
    width: 28px;
    height: 4px;
    background-color: #ffffff;
    transform: rotate(45deg);
  }
  .main-nav--opened .main-nav__toggle::after {
    position: absolute;
    content: "";
    top: 26px;
    right: 18px;
    width: 28px;
    height: 4px;
    background-color: #ffffff;
    transform: rotate(-45deg);
  }
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--closed .main-nav__toggle::before {
    position: absolute;
    content: "";
    top: 17px;
    right: 20px;
    width: 23px;
    height: 4px;
    background-color: #81b3d2;
    box-shadow: 0 9px 0 0 #81b3d2, 0 18px 0 0 #81b3d2;
  }
  .main-nav--nojs .main-nav__list {
    position: static;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
